import mongoose from 'mongoose';
import User from '../model/User';
import Thread from '../model/Thread';
import Comment from '../model/Comment';

const threadAPIs = (app) => {
  app.post(
    '/api/threads', // 
    async (req, res) => {
      try {
        const {
          username,
          token,
          title,
          content,
        } = req.body;
        if (!username) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!token) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!title) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!content) {
          return res.status(400).json({
            message: '',
          });
        }
        const user = await User.findOne({
          username,
          token,
        });
        if (!user) {
          return res.status(400).json({
            message: 'token',
          });
        }
        const newThread = {
          title,
          content,
          author: user,
        };
        const thread = new Thread(newThread);
        await thread.save();
        user.threads.push(thread);
        await user.save();
        return res.json({
          message: '',
        });
      } catch (e) {
        return res.status(400).json({
          message: e.message,
        });
      }
    },
  );

  app.post(
    '/api/comments/:tid', // 
    async (req, res) => {
      try {
        const {
          tid,
        } = req.params;
        const {
          username,
          token,
          content,
        } = req.body;
        if (!tid) {
          return res.status(400).json({
            message: 'ID',
          });
        }
        if (!username) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!token) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!content) {
          return res.status(400).json({
            message: '',
          });
        }
        const user = await User.findOne({
          username,
          token,
        });
        if (!user) {
          return res.status(400).json({
            message: 'token',
          });
        }
        const thread = await Thread.findById(tid);
        if (!thread) {
          return res.status(400).json({
            message: '',
          });
        }
        const newComment = {
          content,
          author: user,
          target: tid,
        };
        const comment = new Comment(newComment);
        await comment.save();
        return res.json({
          message: '',
        });
      } catch (e) {
        return res.status(400).json({
          message: e.message,
        });
      }
    },
  );

  app.get(
    '/api/threads',
    async (req, res) => {
      try {
        const threads = await Thread.find()
          .populate('author', 'username avatar');
        return res.json({
          data: threads,
        });
      } catch (e) {
        return res.status(400).json({
          message: e.message,
        });
      }
    },
  );

  app.get(
    '/api/threads/:tid',
    async (req, res) => {
      try {
        const {
          tid,
        } = req.params;
        if (!tid) {
          return res.status(400).json({
            message: 'ID',
          });
        }
        const thread = await Thread.findById(tid)
          .populate('author', 'username description avatar');
        if (!thread) {
          return res.status(400).json({
            message: '',
          });
        }
        const comments = await Comment.find({ target: tid })
          .populate('author', 'username avatar description');
        return res.json({
          data: { thread, comments },
        });
      } catch (e) {
        return res.status(400).json({
          message: e.message,
        });
      }
    },
  );

  app.patch(
    '/api/threads/:tid',
    async (req, res) => {
      try {
        const {
          tid,
        } = req.params;
        const {
          username,
          token,
          title,
          content,
        } = req.body;
        if (!tid) {
          return res.status(400).json({
            message: 'ID',
          });
        }
        if (!username) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!token) {
          return res.status(400).json({
            message: 'token',
          });
        }
        if (!title) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!content) {
          return res.status(400).json({
            message: '',
          });
        }
        const user = await User.findOne({
          username,
          token,
        });
        if (!user) {
          return res.status(400).json({
            message: 'token',
          });
        }
        const thread = await Thread.findById(tid);
        if (!thread) {
          return res.status(400).json({
            message: '',
          });
        }
        if (String(user._id) !== String(thread.author)) {
          return res.status(400).json({
            message: '',
          });
        }
        thread.title = title;
        thread.content = content;
        await thread.save();
        return res.json({
          message: '',
        });
      } catch (e) {
        return res.status(400).json({
          message: e.message,
        });
      }
    },
  );

  app.delete(
    '/api/threads/:tid',
    async (req, res) => {
      try {
        const {
          tid,
        } = req.params;
        if (!tid) {
          return res.status(400).json({
            message: 'ID',
          });
        }
        const {
          username,
          token,
        } = req.body;
        if (!username) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!token) {
          return res.status(400).json({
            message: 'token',
          });
        }
        const user = await User.findOne({
          username,
          token,
        });
        if (!user) {
          return res.status(400).json({
            message: 'token',
          });
        }
        const thread = await Thread.findById(tid);
        if (!thread) {
          return res.status(400).json({
            message: '',
          });
        }
        if (String(user._id) !== String(thread.author)) {
          return res.status(400).json({
            message: '',
          });
        }
        await Comment.deleteMany({
          target: mongoose.Types.ObjectId(tid),
        });
        user.threads.pull(thread);
        await user.save();
        await thread.remove();
        return res.json({
          message: '',
        });
      } catch (e) {
        return res.status(400).json({
          message: e.message,
        });
      }
    },
  );
};

export default threadAPIs;
