import React, { useState } from 'react';
import { Form, Button, Modal } from 'react-bootstrap';
import { HOST, PORT, DOMAIN } from '../config';

const PostForm = () => (
  <Form id="postForm">
    <Form.Group controlId="title">
      <Form.Label></Form.Label>
      <Form.Control type="text" placeholder="" />
    </Form.Group>
    <Form.Group controlId="content">
      <Form.Label></Form.Label>
      <Form.Control as="textarea" rows="3" />
    </Form.Group>
  </Form>
);

const PostButton = (props) => { // props
  const { loadThreads } = props;// 
  const [show, setShow] = useState(false);
  const showModal = () => setShow(true);
  const closeModal = () => setShow(false);
  const post = async (body) => {
    try {
      const res = await fetch(`${HOST}:${PORT}/api/threads`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(body),
      });
      const result = await res.json();
      if (res.ok) {
        alert(result.message);
        closeModal();
        loadThreads();
      } else {
        alert(result.message);
      }
    } catch (err) {
      alert(err.message);
    }
  };
  const handlePost = async () => {
    const { username, token } = JSON.parse(await localStorage.getItem(DOMAIN));
    const form = document.forms.postForm;
    const title = form.title.value;
    const content = form.content.value;
    const body = {
      username, token, title, content,
    };
    post(body);
  };
  return (
    <div>
      <Button variant="outline-success" size="sm" onClick={() => showModal()}>
                
      </Button>
      <Modal show={show} onHide={() => closeModal()}>
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <PostForm />
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => closeModal()}>
                        
          </Button>
          <Button variant="primary" onClick={() => handlePost()}>
                        
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default PostButton;
