import React from 'react';
import { Button } from 'react-bootstrap';
import { withRouter } from 'react-router-dom';
import { HOST, PORT, DOMAIN } from '../config';

const DeleteButton = (props) => {
  const { tid, history } = props;
  const handle = async (body) => {
    try {
      const res = await fetch(
        `${HOST}:${PORT}/api/threads/${tid}`,
        {
          method: 'DELETE',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(body),
        },
      );
      const result = await res.json();
      if (res.ok) {
        alert(result.message);
        history.push('/threads');
      } else {
        alert(result.message);
      }
    } catch (err) {
      alert(err.message);
    }
  };
  const handleDelete = async () => {
    if (confirm('')) {
      const { username, token } = JSON.parse(localStorage.getItem(DOMAIN));
      const body = { username, token, tid };
      handle(body);
    }
  };
  return (
    <Button variant="outline-danger" size="sm" onClick={() => handleDelete()}></Button>
  );
};

export default withRouter(DeleteButton);
