import Crypto from 'crypto';
import uuid from 'uuid';
import multer from 'multer';

import User from '../model/User';

const upload = multer({
  dest: './static/upload',
});

const userAPIs = (app) => {
  app.get('/api/users', async (req, res) => {
    try {
      const users = await User.find();
      return res.json({
        data: users,
      });
    } catch (e) {
      return res.status(400).json({
        message: e.message,
      });
    }
  });

  app.post(
    '/api/users',
    async (req, res) => {
      try {
        const {
          username,
          password,
          confirmpass,
        } = req.body;
        if (!username) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!password) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!confirmpass) {
          return res.status(400).json({
            message: '',
          });
        }
        if (password !== confirmpass) {
          return res.status(400).json({
            message: '',
            data: 'confirmpass',
          });
        }
        const user = await User.findOne({
          username,
        });
        if (user) {
          return res.status(400).json({
            message: '',
          });
        }
        const passwordCryp = Crypto.createHash('sha1').update(password).digest('hex');// sha1
        const newUser = {
          username,
          password: passwordCryp,
        };
        const newuser = new User(newUser);
        await newuser.save();
        return res.json({
          message: '',
        });
      } catch (e) {
        return res.status(400).json({
          message: e.message,
        });
      }
    },
  );

  app.post(
    '/api/users/login',
    async (req, res) => {
      try {
        const {
          username,
          password,
        } = req.body;
        if (!username) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!password) {
          return res.status(400).json({
            message: '',
          });
        }
        const passwordCryp = Crypto.createHash('sha1').update(password).digest('hex');// 
        const user = await User.findOne({
          username,
        });// 
        if (!user) { // 
          return res.status(400).json({
            message: '',
          });
        }
        if (user.password !== passwordCryp) { // 
          return res.status(400).json({
            message: '',
          });
        }
        const tokenstr = uuid.v4();// 
        const token = Crypto.createHash('sha1').update(tokenstr).digest('hex');// 
        user.token = token;// token
        await user.save();// token
        return res.json({
          message: '',
          data: {
            username: user.username,
            token: user.token,
          },
        });
      } catch (e) {
        return res.status(400).json({
          message: e.message,
        });
      }
    },
  );

  app.post(
    '/api/users/auth',
    async (req, res) => {
      try {
        const {
          username,
          token,
        } = req.body;
        if (!username) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!token) {
          return res.status(400).json({
            message: 'token',
          });
        }
        const user = await User.findOne({
          username,
          token,
        });
        if (!user) {
          return res.status(400).json({
            message: 'token',
          });
        }
        user.password = null;
        user.token = null;
        return res.json({
          message: '',
          data: user,
        });
      } catch (e) {
        return res.status(400).json({
          message: e.message,
        });
      }
    },
  );

  app.post(
    '/api/users/logout',
    async (req, res) => {
      try {
        const {
          username,
          token,
        } = req.body;
        if (!username) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!token) {
          return res.status(400).json({
            message: 'token',
          });
        }
        const user = await User.findOne({
          username,
          token,
        });
        if (!user) {
          return res.status(400).json({
            message: 'token',
          });
        }
        user.token = null;
        await user.save();
        return res.json({
          message: '',
        });
      } catch (e) {
        return res.status(400).json({
          message: e.message,
        });
      }
    },
  );

  app.patch(
    '/api/users',
    upload.single('avatar'),
    async (req, res) => {
      try {
        const {
          username, token, description,
        } = req.body;
        if (!username) {
          return res.status(400).json({
            message: '',
          });
        }
        if (!token) {
          return res.status(400).json({
            message: 'token',
          });
        }
        const user = await User.findOne({
          username,
          token,
        });
        if (!user) {
          return res.status(400).json({
            message: 'token',
          });
        }
        if (req.file) {
          user.avatar = req.file.filename;
        }
        user.description = description;
        await user.save();
        return res.json({ message: '' });
      } catch (e) {
        return res.status(400).json({ message: e.message });
      }
    },
  );

  app.get('/api/users/:username', async (req, res) => {
    try {
      const {
        username,
      } = req.params;
      if (!username) {
        return res.status(400).json({
          message: '',
        });
      }
      const user = await User.findOne({ username })
        .populate('threads', 'title posttime');
      if (!user) {
        return res.status(400).json({
          message: '',
        });
      }
      user.password = null;
      user.token = null;
      return res.json({
        data: user,
      });
    } catch (e) {
      return res.status(400).json({
        message: e.message,
      });
    }
  });
};

export default userAPIs;
